import * as yup from 'yup';

export const loginSchema = yup.object().shape({
    email: yup
        .string()
        .required('Email khng c  trng')
        .email('Email khng hp l')
        .lowercase()
        .trim(),
    password: yup
        .string()
        .required('Mt khu khng c  trng')
        .min(6, 'Mt khu phi c t nht 6 k t'),
});

export const registerSchema = yup.object().shape({
    name: yup
        .string()
        .required('Tn khng c  trng')
        .min(5, 'Tn phi c t nht 5 k t')
        .max(30, 'Tn c ti a 30 k t'),

    email: yup
        .string()
        .required('Email khng c  trng')
        .email('Email khng hp l')
        .lowercase()
        .trim(),
    password: yup
        .string()
        .required('Mt khu khng c  trng')
        .min(6, 'Mt khu phi c t nht 6 k t'),
    confirmPassword: yup
        .string()
        .required('Phi nhp li mt khu')
        .oneOf([yup.ref('password'), null], 'Mt khu nhp li khng ng'),
});

export const forgotPasswordSchema = yup.object().shape({
    email: yup
        .string()
        .required('Email khng c  trng')
        .email('Email khng hp l')
        .lowercase()
        .trim(),
});

export const verifyCodeSchema = yup.object().shape({
    verifyCode: yup
        .string()
        .required('Khng c  trng')
        .max(6, 'M xc nhn c ti a 6 k t'),
});

export const resetPasswordSchema = yup.object().shape({
    password: yup
        .string()
        .required('Mt khu khng c  trng')
        .min(6, 'Mt khu phi c t nht 6 k t'),
    confirmPassword: yup
        .string()
        .required('Phi nhp li mt khu')
        .oneOf([yup.ref('password'), null], 'Mt khu nhp li khng ng'),
});

export const editPasswordSchema = yup.object().shape({
    oldPassword: yup
        .string()
        .required('Mt khu khng c  trng')
        .min(6, 'Mt khu phi c t nht 6 k t'),
    password: yup
        .string()
        .required('Mt khu khng c  trng')
        .min(6, 'Mt khu phi c t nht 6 k t'),
    confirmPassword: yup
        .string()
        .required('Phi nhp li mt khu')
        .oneOf([yup.ref('password'), null], 'Mt khu nhp li khng ng'),
});

export const changeNameSchema = yup.object().shape({
    name: yup
        .string()
        .required('Khng c  trng')
        .min(5, 'Tn phi c t nht 5 k t')
        .max(30, 'Tn c ti a 30 k t')
        .trim(),
});
