import * as yup from 'yup';

export const createClassSchema = yup.object().shape({
    name: yup
        .string()
        .required('Khng c b trng')
        .min(6, 'Tn lp phi c t nht 6 k t')
        .max(50, 'Ti a 50 k t')
        .trim(),
    subject: yup.string().required(),
    sharing: yup.string().required(),
});
