import * as yup from 'yup';

export const practiceSchema = yup.object().shape({
    title: yup.string().required('Tiu  khng c  trng').trim(),
    category: yup.string().required(),
    level: yup.string().required(),
    limitTime: yup.boolean(),
    timeLimited: yup
        .number()
        .nullable()
        .when('limitTime', {
            is: (value) => value === true,
            then: (rule) =>
                rule.required().min(1, 'Thi gian gii hn phi ln hn 0'),
        }),
    sharing: yup.string().required(),
    classId: yup
        .string()
        .nullable()
        .when('sharing', {
            is: (value) => value === 'CLASS',
            then: (rule) => rule.required(),
        }),
});

export const answerFormSchema = yup.object().shape({
    answer: yup.string().required('Phi nhp cu tr li').lowercase().trim(),
});
